import React from 'react';
import './info.scss'
import Typical from 'react-typical'


class Info extends React.Component<any> {
    infoData:any = this.props.infoData;

    description: any = [{
        key: 'Age',
        value: this.getAge(new Date(this.infoData.dob))
    },
    {
        key: 'Phone',
        value: this.infoData.phone
    },
    {
        key: 'Skype Id',
        value: this.infoData.skypeId
    },
    {
        key: 'Email',
        value: this.infoData.emailId
    }
    ]
    getAge(dob) {
        var today = new Date();
    var birthDate = new Date(dob);  // create a date object directly from `dob1` argument
    var age_now = today.getFullYear() - birthDate.getFullYear();
    var m = today.getMonth() - birthDate.getMonth();
    if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) 
    {
        age_now--;
    }
    console.log(age_now);
    return age_now; 
    }
    render(): React.ReactNode {
        return (
            <>
            <div className="info">
                <div className="container">
                    <div className='info__wrapper'>
                        <div className="info__user-image">
                            <img src={this.infoData.profileUrl} alt='as' />
                        </div>
                        <div className="info__name--section">
                            <div className="app-column">
                                <span className="info__name">{this.infoData.name}</span>
                           {  this.infoData.typicalHeading && <h2 className='info__heading'>
                                I'm {' '}
                              <Typical
                                    steps={this.infoData.typicalHeading}
                                    loop={Infinity}
                                    wrapper="span"
                                />
                              </h2>}
                            </div>
                            <div className="mt-20">
                                {
                                    this.description.map((item: any, index: number) => {
                                        return (
                                            <div className="info__discryption" key={index}>
                                                <span className="info__discryption--heading">{item.key}:</span>
                                                <span>{item.value}</span>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            <div>
                                <a href={this.infoData.linkedInUrl} target="_blank" rel="noreferrer"> <i
                                    className="fab fa-linkedin info__icon"></i></a>
                                <a href={this.infoData.facebookUrl} target="_blank" rel="noreferrer"> <i
                                    className="fab fa-facebook-f info__icon"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
          <div className="container" id="resumes" key={'l'}>
          <div className="info-next__description">Hi</div>
          <p className="info-next__description--text">{this.infoData.myInfo}
          </p>
          <a className="app-btn" href={this.infoData.resumeLink}
            target="_blank" rel="noreferrer">
            <i className="fas fa-download pr-10"></i>
            Download CV</a>
        </div>
       </>
        );
    }
}
export default Info