import React from 'react';
import './home.scss';
import Header from '../header/header';
import Info from '../info/info';
import Timeline from '../timeline/timeline';
import ProgressBar from '../progress-bar/progress-bar';
import Card from '../card/card';
import Footer from '../footer/footer';

export class Home extends React.Component<any> {
  constructor(props:any) {
    super(props)
    console.log(props)
  }
  data: any = 'Education';
  homeData:any = this.props.profileData;
  
  educationTimeLine: any = {
    title: 'Education',
    timeLineData: this.homeData?.educationList
  }
  workExperianceTimeLine: any = {
    title: 'Work EXPERIANCE',
    timeLineData: this.homeData?.workExpList
  }
  render(): React.ReactNode {
    return (
      <div className="home">
        <Header key={'x'} header={this.props.profileData.headerList} isSuperAdmin={this.props.profileData.isSuperAdmin}></Header>
        <Info key={'y'} infoData={this.props.profileData}></Info>
       { this.homeData.educationList &&
        <Timeline key={'p'} data={this.educationTimeLine}></Timeline>}
       { this.homeData.workExpList &&
        <Timeline key={'w'} data={this.workExperianceTimeLine}></Timeline>}
        <div className="container">
          <div className="home__heading">
            Technical skills
          </div>
          {
            this.homeData.technicalSkillsList.map((skill: any, i: number) => {
              return (
                <div key={i}>
                  <div className="home__sub--heading">
                    {skill.skillType}
                  </div>
                  <div className="home__wrapper">
                    {
                      skill.skillsList.map((item: any, index: number) => {
                        return (
                          <ProgressBar key={index + i} name={item.name} rating={item.rating}></ProgressBar>
                        )
                      })

                    }
                  </div>
                </div>
              )
            })
          }
        </div>
        <div className="home__projects container" key={'r'}>
          <div className="home__heading">
            My Projects
          </div>
          <div className="home__projects--list">
            {
              this.homeData.projectList.map((project:any, index: number) => {
                return (
                  <Card cardData={project} key={index}></Card>
                )
              })
            }
          </div>
        </div>
        <div id="contacts">
        <Footer footerData={this.homeData}></Footer>
        </div>
      </div>
    );
  }
}

export default Home;