import React from 'react';
import './header.scss';
import { Link } from 'react-router-dom';

class Header extends React.Component<any> {

  navbar: any = document.getElementById("navbar");
  headerData = this.props.header;
  constructor(props:any) {
    super(props);
    this.state = {date: new Date()};
    console.log(this.props)
   
  }

  componentDidMount() {
    window.addEventListener('scroll', this.runOnScroll);
  }
 

  render() {
  return (
    <div className='header'>
      <div className="header__navbar" id='navbar'>
        <div className="container">
          {
            this.headerData.map((data: any, index:number) =>
              <a className="header__navbar--item active resume" href="index.html#resumes" key={index}>{data.name}</a>
            )
          }
        { 
        this.props.isSuperAdmin ?
          <div className="header__your--portfolio">
        <Link className="header__navbar--item header__your--portfolio-link"  to={'/header-option'}>Get your Portfolio</Link>
          </div>  :
          <div className="header__your--portfolio">
            <span className='header__navbar--item header__your--portfolio-link'>Please contact - 8147068760 to get this</span>
          </div>
          }       
        </div>
      </div>
    </div>

  );
}

runOnScroll() {
    
      const scrollTop = document.documentElement.scrollTop;
      if(this.navbar) {
        if (scrollTop> 0) {
          this.navbar.classList.add("header__sticky");
        } else {
          this.navbar.classList.remove("header__sticky")
        }
      }
  
  }

}

export default Header;